.box {
  font-size: 12px;
}
.imgSize {
  width: 18px;
  margin: 0px 4.2px;
}
.serch {
  margin-top: 30px;
}
.tableborder {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 30px;
}
.page {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  line-height: 28px;
  .pagenum {
    margin-right: 10px;
  }
}
.iconfont {
  font-size: 20px;
  color: #067fb5;
}
.fontcolor {
  color: #067fb5;
  cursor: pointer; // 鼠标移上去变小手
}
.tips{
  padding: 20px;
  &>div{
    margin: 10px 0;
  }
}
</style>
